.App .main {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  /* font-family: myFont; */
}

#gameScreen {
  border: solid 1px black;
}


audio {
  display: none;
}

form {
  width: 20em;
}

button {
  margin-right: 10px;
}

.speedBtn, #resetBtn, #nextBtn {
  border: solid 1px black;
  border-radius: 20px;
  margin-left: 10px;
  margin-right: 10px;
}
#resetBtn, #nextBtn {
  position: absolute;
  bottom: 20px;
  right: 300px;
  padding-left: 40px;
  padding-right: 40px;
  z-index: 10;
}
#nextBtn {
  right: 40px;
}

@font-face {
  font-family: "harryPotterFont";
  src: local("harryPotterFont"), url("./harryPotterFont.TTF") format("truetype");
}
@font-face {
  font-family: "bladeRunnerFont";
  src: local("bladeRunnerFont"), url("./bladeRunnerFont.TTF") format("truetype");
}
@font-face {
  font-family: "goodTimesFont";
  src: local("goodTimesFont"), url("./goodTimesFont.ttf") format("truetype");
}


/* sound effect input switch */
.--light {
  background: #c7c7cc;
}
.--light:checked {
  background: #34c759;
}
.ios-switch {
  appearance: none;
  outline: none;
  border: 0;
  border-radius: 1em;
  position: relative;
  width: 3.4em;
  height: 2em;
  overflow: hidden;
}
.ios-switch:after {
  content: "";
  position: absolute;
  width: 1.6em;
  height: 1.6em;
  top: 0.2em;
  left: 0.2em;
  background: white;
  border-radius: 50%;
  transition: all 0.1s;
  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.2);
}
.ios-switch:checked:after {
  transform: translateX(1.4em);
  box-shadow: -1px 0 5px rgba(0, 0, 0, 0.2);
}

#gameScreenDiv {
  position: relative;
  padding-left: 5px;
}
#canvasGameScreen {
  position: relative;
}
#gameScreenCorrectAnswer, #correctAnswer {
  top: 15px;
  position: absolute;
  font-family: goodTimesFont;
  left: 20px;
}
#correctAnswer table td, #correctAnswer table th {
  /* C_H (500) / 13 */
  height: 34px;
  border: solid 1px black;
  text-align: center;
  width: 76px;
}
.showScores {
  font-family: "goodTimesFont";
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  min-width: 1000px;
}
#userScores, #highScores {
  border: 1px solid black;
  border-radius: 25px;
  padding: 25px;
  margin-top: 50px;
  height: 80%;
  min-width: 400px;
}
#userScores {
  margin-left: 50px;
  width: 45%;
}
#scoresBreakdown, #highScoresList {
  font-size: 1.2em;
  padding-left: 30px;
}
#scoresBreakdown td {
  padding-right: 10px;
}
.showScores h3, #totalScore {
  font-size: 2em;
}
#highScores {
  right: 50px;
  position: absolute;
  top: 0px;
  width: 45%;
}
#highScoresList table {
  width: 100%;
}

.store {
  overflow: auto;
  display: flex;
}
.store .character {
  float: left;
  border: solid 1px black;
  border-radius: 25px;
  padding: 10px;
  margin-right: 10px;
  text-align: center;
  width: 180px;
  height: 250px;
  position: relative;
}
.store .character h5, .store .character img, .store .character button {
  position: absolute;
}
.store .character h5 {
  top: 20px;
  width: 85%;
}
.store .character img {
  top: 70px;
  width: 100px;
  height: 100px;
  left: 40px;
}
.store .character button {
  bottom: 10px;
  left: 10px;
  width: 90%;
  border-radius: 20px;
}

#inputAns {
  position: absolute;
  z-index: 10;  
  width: 76px;
  height: 34px;
  font-size: 20px;
  text-align: center;
  font-family: goodTimesFont;
}